const currentJobReducer = (state = {}, action) => {
    switch(action.type) {
        case 'CURRENT_JOB': {
            return { name:action.payload, items: []};
        }
        case 'JOB_INSTANCES' : {
            return {...state, instances: action.payload} 
        }
        case 'CURRENT_INSTANCE': {
            return {...state, currentInstance: action.payload}
        }
        case 'JOB_EXECUTIONS': {
            return {...state, executions: action.payload.executions}
        }
        default : {
            return state;
        }
    }
}
export default currentJobReducer;