import jobDefinitionReducer from './jobDefinition';
import isConnectedReducer from './isConnected';
import jobNamesReducer from './jobNames';
import currentJobReducer from './currentJob';
import {combineReducers} from 'redux';

const allReducers = combineReducers(
    {
        jobDefinition: jobDefinitionReducer, 
        isConnected: isConnectedReducer,
        jobNames: jobNamesReducer,
        currentJobData: currentJobReducer
    });

export default allReducers;