import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

import "assets/plugins/nucleo/css/nucleo.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import "assets/scss/argon-dashboard-react.scss";

import AdminLayout from "layouts/Admin.js";
import { createStore } from 'redux';
import allReducers from 'redux/reducers';
import { Provider } from 'react-redux';
import {connect} from './redux/actions';
import {loadJobDefinitions, loadJobNames, reloadOnJobEvent, reloadOnPing, host} from 'api';

// Store
const store = createStore(allReducers, {    isConnected: true },
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);
/*store.subscribe(
  () => console.log(store.getState())
);*/

ReactDOM.render(
  <Provider store={store}>
    <BrowserRouter>
      <Switch>
        <Route path="/jobs" render={props => <AdminLayout {...props} />} />
        <Redirect from="/" to="/jobs/index" />
      </Switch>
    </BrowserRouter>
  </Provider>,
  document.getElementById("root")
);

const sse = new EventSource(host+'/events/register-client');

sse.onopen = function() {
  store.dispatch(connect(true));
};

sse.onerror = function(error) {
  console.error(error);
  store.dispatch(connect(false));
};

sse.addEventListener("ping", function(e) {
  reloadOnPing(store)
});


sse.addEventListener("jobs", function(e) {
  reloadOnJobEvent(store);
});

loadJobDefinitions(store);
loadJobNames(store);