import {setJobDefinitions, setJobNames} from '../redux/actions'
//export const host = 'http://localhost:8082'
export const host = ''
const urlPrefix = host+'/job-monitoring'

export const loadJobDefinitions = (store) => {
    fetch(urlPrefix+'/job-definitions')
        .then((response) => response.json())
        .then((data) => {
            store.dispatch(setJobDefinitions(data.value));
        })
        .catch(function () {
            store.dispatch(setJobDefinitions(0));
        });
};

export const loadJobNames = (store) => {
    fetch(urlPrefix+'/job-names')
        .then((response) => response.json())
        .then((data) => {
            store.dispatch(setJobNames(data.value));
        })
        .catch(function () {
            store.dispatch(setJobNames([]));
        });
};

export const loadJobInstancesAndDispatch = (jobName, dispatch) => {
    fetch(urlPrefix+'/job/'+jobName+'/instances')
        .then((response) => response.json())
        .then((data) => {
            dispatch({ type: 'JOB_INSTANCES', payload: data.value});
        })
        .catch(function () {
            console.log('Unable to load job instaces');
        });
};


export const loadJobExecutionsAndDispatch = (instanceId, dispatch) => {
    fetch(urlPrefix+'/job/instance/'+instanceId)
        .then((response) => response.json())
        .then((data) => {
            dispatch({ type: 'JOB_EXECUTIONS', payload: data.value});
        })
        .catch(function () {
            console.log('Unable to load job executions');
        });
};

export const triggerNewJobWithParameter = (jobName) => {
    fetch(urlPrefix+'/job/start-new/?jobName='+jobName, {
        method: "POST"
        });
}

export const reloadOnJobEvent= (store) => {
    const state = store.getState();
    if (state.currentJobData.name) {
        loadJobInstancesAndDispatch(state.currentJobData.name, store.dispatch);
    }
    if (state.currentJobData.currentInstance) {
        loadJobExecutionsAndDispatch(state.currentJobData.currentInstance, store.dispatch);
    }
}

export const reloadOnPing= (store) => {
    loadJobDefinitions(store);
    
    const state = store.getState();   
    if (state.currentJobData.currentInstance) {
        loadJobExecutionsAndDispatch(state.currentJobData.currentInstance, store.dispatch);
    }
}

export const stopExecution = (executionId) => {
    fetch(urlPrefix+'/execution/'+executionId+'/stop', {
        method: "POST"
        });
}


export const restartExecution = (executionId) => {
    fetch(urlPrefix+'/execution/'+executionId+'/restart', {
        method: "POST"
        });
}

export const runSimpleJob = (parameter) => {
    fetch(urlPrefix+'/simple-job/start/'+parameter, {
        method: "POST"
        });
}


