import React from "react";
import {useSelector, useDispatch} from 'react-redux'
import {loadJobInstancesAndDispatch, runSimpleJob} from '../../api'


// reactstrap components
import { Card, CardBody, CardTitle, Container, Row, Col,
  Input
} from "reactstrap";

const Header = () => {

    const dispatch = useDispatch()
    const numberOfJobDefinitions = useSelector( state => state.jobDefinition);
    let jobNames = useSelector(state => state.jobNames)
    if (!jobNames) {
      jobNames = [];
    }

    return (
      <>
        <div className="header bg-gradient-info pb-8 pt-5 pt-md-8">
          <Container fluid>
            <div className="header-body">
              {/* Card stats */}
              <Row>
                <Col lg="6" xl="6">
                  <Card className="card-stats mb-4 mb-xl-0">
                    <CardBody>
                      <Row>
                        <div className="col">
                          <CardTitle
                            tag="h5"
                            className="text-uppercase text-muted mb-0"
                          >
                            Job Definitions
                          </CardTitle>
                          <span className="h2 font-weight-bold mb-0">
                            {numberOfJobDefinitions}
                          </span>
                        </div>
                        <Col className="col-auto">
                          <div className="icon icon-shape bg-danger text-white rounded-circle shadow">
                            <i className="fas fa-project-diagram" />
                          </div>
                        </Col>
                      </Row>
                      <p className="mt-3 mb-0 text-muted text-sm">
                        {jobNames.map(jobName => 
                          <span key={jobName} className="jobLink">
                           <a href="#selectJobName" onClick={() => {
                                dispatch({ type: 'CURRENT_JOB', payload: jobName })
                                loadJobInstancesAndDispatch(jobName,dispatch);
                              }
                              }>
                             {jobName}
                           </a>
                          </span>
                        )}                                                                          
                      </p>
                    </CardBody>
                  </Card>
                </Col>
                <Col lg="6" xl="6">
                  <Card className="card-stats mb-4 mb-xl-0">
                    <CardBody>
                      <Row>
                        <div className="col">
                          <CardTitle
                            tag="h5"
                            className="text-uppercase text-muted mb-0"
                          >
                            Run simpleJob with parameters
                          </CardTitle>
                          <span className="h2 font-weight-bold mb-0">
                          <Input id="param" type="text" cl placeholder="Parameter" bsSize="sm" />                          
                          </span>
                        </div>
                        <Col className="col-auto">
                          <div className="icon icon-shape bg-yellow text-white rounded-circle shadow">
                            <i className="fas fa-play" />
                          </div>
                        </Col>
                      </Row>
                      <p className="mt-3 mb-0 text-muted text-sm">
                        <span className="text-success mr-2">
                        <button onClick={() => runSimpleJob( document.getElementById('param').value)} className="btn btn-sm">Run Job</button>
                        </span>                        
                      </p>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
            </div>
          </Container>
        </div>
      </>
    );
  
}

export default Header;
