import React from "react";
import { useSelector, useDispatch } from 'react-redux'
import { loadJobExecutionsAndDispatch, triggerNewJobWithParameter, stopExecution, restartExecution } from '../api'
// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  Table,
  Container,
  Row,
  Col
} from "reactstrap";


// core components

import Header from "components/Headers/Header.js";

// A custom hook that builds on useLocation to parse
// the query string for you.
/*
import {
  useLocation
} from "react-router-dom";


function useQuery() {
  return new URLSearchParams(useLocation().search);
}*/

const Index = () => {
  const dispatch = useDispatch();
  const currentJobData = useSelector(state => state.currentJobData);
  const currentJobName = currentJobData.name;
  const currentInstance = currentJobData.currentInstance;
  const jobInstances = currentJobData.instances ? currentJobData.instances : [];
  const jobExecutions = currentJobData.executions ? currentJobData.executions : null;

  // JobList
  return (
    <>
      <Header />
      {/* Page content */}
      <Container className="mt--7" fluid>
        <Row className="mt-5">
          {currentJobName && (
            <Col className="mb-5 mb-xl-0" xl="6">
              <Card className="shadow">
                <CardHeader className="border-0">
                  <Row className="align-items-center">
                    <div className="col">
                      <h3 className="mb-0">Job: {currentJobName}</h3>
                    </div>
                    <div className="col text-right">
                      <Button
                        color="primary"
                        href="#pablo"
                        onClick={e => {
                          e.preventDefault();
                          triggerNewJobWithParameter(currentJobName);
                        }}
                        size="sm"
                      >
                        Trigger new Job without parameters
                      </Button>
                    </div>
                  </Row>
                </CardHeader>
                <Table className="align-items-center table-flush" responsive>
                  <thead className="thead-light">
                    <tr>
                      <th scope="col">Instance Id</th>
                      <th scope="col">Version</th>
                    </tr>
                  </thead>
                  <tbody>
                    {jobInstances.map(jobInstance =>
                      <tr key={jobInstance.id}>
                        <th scope="row">
                          <a href="#selectInstance" onClick={() => {
                            dispatch({ type: 'CURRENT_INSTANCE', payload: jobInstance.id })
                            loadJobExecutionsAndDispatch(jobInstance.id, dispatch);
                          }
                          }>
                            Instance {jobInstance.id}
                          </a>
                        </th>
                        <td>{jobInstance.version}</td>
                      </tr>)
                    }

                  </tbody>
                </Table>
              </Card>
            </Col>)}
          {jobExecutions && (
            <Col className="mb-5 mb-xl-0" xl="6">
              <Card className="shadow">
                <CardHeader className="border-0">
                  <Row className="align-items-center">
                    <div className="col">
                      <h3 className="mb-0">Job Executions for Instance: {currentInstance}</h3>
                    </div>
                    <div className="col text-right">
                    </div>
                  </Row>
                </CardHeader>
                <Table className="align-items-center table-flush" responsive>
                  <thead className="thead-light">
                    <tr>
                      <th scope="col">Status</th>
                      <th scope="col">Running</th>
                      <th scope="col">ContextInfo</th>
                      <th scope="col">Action</th>                      
                    </tr>
                  </thead>
                  <tbody>
                    {jobExecutions.map(execution =>
                      <tr key={execution.id}>                        
                          <td>{execution.status}</td>                          
                          <td>
                            {execution.isRunning && (
                            <i className="fas fa-spinner ni spin text-success mr-3" />
                          )} 
                          {!execution.isRunning && ( 
                            <>not running</>
                          )}                          
                          </td>
                          <td>{execution.contextInfo}</td>
                          <td>
                            <button className="btn btn-sm" onClick={() => stopExecution(execution.id)}>Stop</button>
                            <button className="btn btn-sm" onClick={() => restartExecution(execution.id)}>Restart</button>
                          </td>                                               
                      </tr>
                    )}                    
                  </tbody>
                </Table>
              </Card>
            </Col>)}
        </Row>


        {!currentJobName &&
          <Row className="mt-5">
            <br></br>          
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
          </Row>}
      </Container>
    </>
  );

}

export default Index;
