export const connect = (isConnected) => {
    if (isConnected) {
        return {
            type: 'CONNECTED'
        }
    } else {
        return {
            type: 'DISCONNECTED'
        }
    }
}
export const  setJobDefinitions = (nrOfResults) => {   
    return {
        type: 'JOB_DEFINITIONS',
        payload: nrOfResults
    }
}

export const  setJobNames = (jobNames) => {   
    return {
        type: 'JOB_NAMES',
        payload: jobNames
    }
}

export const setCurrentJob = (jobName) => {   
    return {
        type: 'CURRENT_JOB',
        payload: jobName
    }
}
